  cursor: pointer !important;
  transition: background-color 0.2s ease !important;
  flex-shrink: 0 !important;
  flex-grow: 0 !important;
  
  /* 強制顯示 */
  display: block !important;
  visibility: visible !important;
  opacity: 1 !important;
  z-index: 999 !important;
}

